import Vue from 'vue'
import Vuex from 'vuex'
import { POSTER_TYPE_INFOS } from '../constants'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    posterImg: null, // 海报 图片
    qrcodeImg: null, // 二维码 图片
    posterType: '0' // 海报模式
  },
  getters: {
    posterTypeInfo: state => {
      return POSTER_TYPE_INFOS[Number(state.posterType)]
    }
  },
  mutations: {
    setPosterImg(state, img) {
      state.posterImg = img
    },
    setQrcodeImg(state, img) {
      state.qrcodeImg = img
    },
    setPosterType(state, value) {
      state.posterType = value
    }
  }
})
