<template>
  <div class="preview-wrapper">
    <div class="common-row operator">
      <mt-button type="primary" size="small" @click="posterTypeSelectionShow = true">选择海报模式</mt-button>
      <mt-button
        type="primary"
        size="small"
        :disabled="disabled"
        @click="draw"
      >{{ base64Img ? '重新生成' : '生成' }}</mt-button>
      <mt-button type="primary" size="small" :disabled="disabled" @click="download">下载</mt-button>
    </div>
    <div class="common-row poster-type-info">
      <p>
        模式:
        <span class="red">{{ posterTypeInfo.name }}</span>
      </p>
    </div>

    <div class="divide" />

    <div v-if="base64Img">
      <img :src="base64Img" />
    </div>

    <!-- 预览区域 -->
    <canvas v-show="false" ref="canvas" />

    <!-- 选择海报模式 -->
    <mt-popup v-model="posterTypeSelectionShow" position="bottom">
      <div class="poster-type-selection">
        <mt-radio title="选择海报模式" v-model="posterType" :options="options" @change="optionChange"></mt-radio>
      </div>
    </mt-popup>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'

import { POSTER_TYPE_INFOS } from '../constants'

export default {
  name: 'Preview',
  data() {
    return {
      ctx: null,
      base64Img: null,
      posterTypeSelectionShow: false,
      options: POSTER_TYPE_INFOS.map((info, index) => ({
        label: info.name,
        value: String(index)
      }))
    }
  },
  computed: {
    ...mapState([
      'posterImg', //
      'qrcodeImg'
    ]),
    ...mapGetters(['posterTypeInfo']),

    disabled() {
      return !this.posterImg || !this.qrcodeImg
    },
    posterType: {
      get() {
        return this.$store.state.posterType
      },
      set(value) {
        return this.$store.commit('setPosterType', value)
      }
    }
  },
  watch: {
    posterImg(val) {
      this.$nextTick(() => {
        this.draw()
      })
    },
    qrcodeImg(val) {
      this.$nextTick(() => {
        this.draw()
      })
    }
  },
  methods: {
    optionChange() {
      this.posterTypeSelectionShow = false
      this.draw()
    },
    draw() {
      if (this.disabled) {
        return
      }

      this.initCanvas()

      // 绘制海报
      this.ctx.drawImage(this.posterImg, 0, 0)

      // 绘制二维码
      const qrcodeInfo = {
        x: this.posterTypeInfo.x * this.posterImg.width,
        y: this.posterTypeInfo.y * this.posterImg.height,
        width: this.posterTypeInfo.width * this.posterImg.width,
        height: this.posterTypeInfo.height * this.posterImg.height
      }
      this.ctx.drawImage(
        this.qrcodeImg,
        qrcodeInfo.x,
        qrcodeInfo.y,
        qrcodeInfo.width,
        qrcodeInfo.height
      )

      const canvas = this.$refs['canvas']
      this.base64Img = canvas.toDataURL('image/png')
    },
    initCanvas() {
      const canvas = this.$refs['canvas']
      const ctx = canvas.getContext('2d')

      canvas.width = this.posterImg.width
      canvas.height = this.posterImg.height
      canvas.style.width = '100%'

      this.ctx = ctx
    },
    download() {
      const userAgent = navigator.userAgent
      if (/wechat/.test(userAgent.toLowerCase())) {
        this.$Toast('请长按图片保存')
      }

      const a = document.createElement('a')
      a.download = `pic_${Date.now()}.png`
      a.href = this.base64Img

      // 方法 1.
      a.click()

      // // 方法 2.
      // const event = document.createEvent('MouseEvents')
      // event.initMouseEvent(
      //   'click',
      //   true,
      //   false,
      //   window,
      //   0,
      //   0,
      //   0,
      //   0,
      //   0,
      //   false,
      //   false,
      //   false,
      //   false,
      //   0,
      //   null
      // )
      // a.dispatchEvent(event)
    }
  }
}
</script>

<style lang="scss" scoped>
.preview-wrapper {
  img {
    width: 100%;
  }
  .operator {
    .mint-button + .mint-button {
      margin-left: 2vw;
    }
  }
  .divide {
    content: ' ';
    margin: 0 auto;
    width: 90%;
    height: 1px;
    background-color: #ddd;
  }
  .poster-type-info {
    p {
      font-weight: bold;
      .red {
        color: red;
      }
    }
  }
  .poster-type-selection {
    width: 100vw;
  }
}
</style>
